@import-normalize;
@import "./header.css";
@import "./home.css";
@import "./about.css";
@import "./portfolio.css";
@import "./skill.css";
@import "./contact.css";
@import "./footer.css";

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;

}

html {
  max-width: 100%;
}

body {
  font-family: "Arima", cursive, sans-serif;
  background-color: #B3DE81;
}

.container {
  max-width: 85%;
  margin: auto;
}

.flexSB{
  display: flex;
  align-items: center;
  justify-content: space-between;
}

button {
  background-color: none;
  border: none;
  outline: none;
  font-weight: bold;
}

.mtop {
  margin-top: 80px;
}

@media (max-width: 800px) {

  html {
    max-width: 100%;
    overflow-x: hidden;
  }

  body {
    word-break: break-all; 
   }

  .home {
    height: 50vh;
  }

  .home h1 {
    font-size: 1.5rem;
    margin-top: 60%;
  }

  .home h3{
    font-size:1rem;
  }

  .contact .row,
  .skill .row,
  .about .row {
    width: 100%;
  }

  .contact .flexSB,
  .skill .flexSB,
  .about .flex {
    flex-direction: column;
  }

  .grid {
    grid-template-columns: repeat(1, 1fr);
  }

  .about .right {
    padding: 20px 30px;
  }

}


@media (max-width: 500px) {

  .home h1{
    margin-top: 80%;
  }

  .about h2{
    font-size: 2.3rem;
  }

  .skill h1{
    font-size: 1.8rem;    
  }

  .contact-me{
    font-size: 1.3rem;
  }
}


